import React, { Component } from "react";
import { Card, Container, ProgressBar } from "react-bootstrap";

export default class Resume extends Component {
  render() {
    let edicationContent = (
      <div className="jumbotron">
        <h2 style={{ padding: "10px" }}>Education</h2>
        <p style={{ padding: "30px" }}>Resume</p>
      </div>
    );

    let workContent = (
      <div className="jumbotron">
        <h2 style={{ padding: "10px" }}>Work</h2>
        <p style={{ padding: "30px" }}>Resume</p>
      </div>
    );

    let skillContent = (
      <div className="jumbotron">
        <h2 style={{ padding: "10px" }}>Skils</h2>
        <p style={{ padding: "30px" }}>Resume</p>
      </div>
    );

    if (this.props.data) {
      console.log(this.props.data);

      edicationContent = (
        <div className="jumbotron">
          <h2 style={{ padding: "10px" }}>Education</h2>
          <p style={{ padding: "30px" }}>
            {this.props.data.education.map((education) => {
              return (
                <Card>
                  <Card.Title>{education.school}</Card.Title>
                </Card>
              );
            })}
          </p>
        </div>
      );

      workContent = (
        <div className="jumbotron">
          <h2 style={{ padding: "10px" }}>Work</h2>
          <p style={{ padding: "30px" }}>
            {this.props.data.work.map((work) => {
              return null;
            })}
          </p>
        </div>
      );

      skillContent = (
        <div className="jumbotron">
          <h2 style={{ padding: "10px" }}>Skills</h2>
          <p style={{ padding: "30px" }}>
            {this.props.data.skills.map((skill) => {
              return (
                <div style={{ padding: "10px" }}>
                  <Card key={skill.name}>
                    <Card.Title>{skill.name}</Card.Title>
                    <Card.Text>
                      <ProgressBar now={skill.level} />
                    </Card.Text>
                  </Card>
                </div>
              );
            })}
          </p>
        </div>
      );
    }

    let resumeContent = (
      <Container>
        {edicationContent}
        {workContent}
        {skillContent}
      </Container>
    );

    return resumeContent;
  }
}
